<template>
  <div class="md-layout wrapper">
    <md-content class="md-layout-item md-xlarge-size-66 md-large-size-66 md-medium-size-100">
        <md-tabs>
            <md-tab id="tab-hospital" md-label="병원">
                <p class="md-headline">{{hospitalData.name}}</p>
                <div class="section">
                    <sui-image-group size="small">
                        <sui-image v-for="image in hospitalImages" :key="image.display_order" :src="image.image_url" />
                    </sui-image-group>
                </div>
                <div class="section">
                    <md-chips v-model="hashtags" md-static></md-chips>
                </div>
                <sui-comment-group>
                  <h3 is="sui-header" dividing>리뷰</h3>
                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/matt.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Matt</a>
                          <sui-comment-metadata>
                          <div>Today at 5:42PM</div>
                          </sui-comment-metadata>
                          <sui-comment-text>How artistic!</sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                      </sui-comment>

                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/elliot.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Elliot Fu</a>
                          <sui-comment-metadata>
                          <div>Yesterday at 12:30AM</div>
                          </sui-comment-metadata>
                          <sui-comment-text>
                          <p>This has been very useful for my research. Thanks as well!</p>
                          </sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                      <sui-comment-group>
                          <sui-comment>
                          <sui-comment-avatar src="static/images/avatar/small/jenny.jpg" />
                          <sui-comment-content>
                              <a is="sui-comment-author">Jenny Hess</a>
                              <sui-comment-metadata>
                              <div>Just now</div>
                              </sui-comment-metadata>
                              <sui-comment-text>
                              Elliot you are always so right :)
                              </sui-comment-text>
                              <sui-comment-actions>
                              <sui-comment-action>Reply</sui-comment-action>
                              </sui-comment-actions>
                          </sui-comment-content>
                          </sui-comment>
                      </sui-comment-group>
                      </sui-comment>

                      <sui-comment>
                      <sui-comment-avatar src="static/images/avatar/small/joe.jpg" />
                      <sui-comment-content>
                          <a is="sui-comment-author">Joe Henderson</a>
                          <sui-comment-metadata>
                          <div>5 days ago</div>
                          </sui-comment-metadata>
                          <sui-comment-text>
                          Dude, this is awesome. Thanks so much
                          </sui-comment-text>
                          <sui-comment-actions>
                          <sui-comment-action>Reply</sui-comment-action>
                          </sui-comment-actions>
                      </sui-comment-content>
                  </sui-comment>
              </sui-comment-group>
            </md-tab>

            <md-tab id="tab-doctor" md-label="의사">
                <sui-card-group :items-per-row="2">
                    <sui-card v-for="(doctor, index) in doctors" :key="index">
                        <sui-card-content>
                        <sui-image :src="doctor.profile_img_url" class="floated doctor-img"/>
                        <sui-card-header>{{doctor.english_name}}</sui-card-header>
                        <sui-card-meta>{{doctor.korean_name}}</sui-card-meta>
                        <sui-card-description>
                            {{doctor.medical_subject.map((e)=>e.medical_subject.name)}}
                        </sui-card-description>
                        </sui-card-content>
                        <sui-card-content extra>
                        </sui-card-content>
                    </sui-card>
                </sui-card-group>
            </md-tab>
        </md-tabs>
    </md-content>
  </div>
</template>
<script>
export default {
  name: 'HospitalInfo',
  components: {
  },
  created () {
    // 병원정보
    this.$http.get(`${this.$api}/hospital_info`, {
      headers: {
        'X-Http-Token': this.$store.state.token
      }
    }).then((result) => {
      console.log(result)
      this.hospitalData = result.data.data.hospital
      this.hashtags = this.hospitalData.hash_tags.map(e => e.hashtag_name)
      this.hospitalImages = this.hospitalData.photos
    })

    // 의사
    this.$http.get(`${this.$api}/doctor`, {
      headers: {
        'X-Http-Token': this.$store.state.token,
        month: '5'
      }
    }).then((result) => {
      console.log(result)
      this.doctors = result.data.data.doctors
    })
  },
  data () {
    return {
      doctors: [],
      hospitalData: null,
      hashtags: [],
      hospitalImages: [],
      cities: [
        'New York',
        'Amsterdam',
        'Tokyo',
        'Rome'
      ]
    }
  },
  computed: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.wrapper{
  margin: 50px 30px;
  .md-content{
      margin: 0 auto;
      .doctor-img{
          width: 80px;
          height: 80px;
          border-radius: 50%;
      }
  }
}
</style>



