<template>
  <div>
    <div class="signup-wrapper">
      <form novalidate class="md-layout" @submit.prevent="validateUser">
        <md-card class="md-layout-item md-size-50 md-medium-size-66">
          <md-card-header>
            <span class="md-display-1">Sign Up</span>
          </md-card-header>
          <md-card-content  class="md-layout md-gutter md-alignment-top">
            <div class="md-layout-item md-size-50 md-small-size-100">
              <div class="section">
                <md-field :class="getValidationClass('email')">
                  <label for="email">이메일</label>
                  <md-input
                    type="email"
                    name="email"
                    id="email"
                    autocomplete="email"
                    v-model="form.email"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">아이디로 사용할 이메일을 입력해주세요.</span>
                  <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                  <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                </md-field>
                <md-field :class="getValidationClass('lastName')">
                  <label for="password">비밀번호</label>
                  <md-input
                    name="password"
                    id="password"
                    type="password"
                    v-model="form.password"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">특수문자 포함 8자이상으로 설정해주세요.</span>
                  <span class="md-error" v-if="!$v.form.password.required">The last name is required</span>
                  <span class="md-error" v-else-if="!$v.form.password.minlength">8자이상으로 설정해주세요.</span>
                </md-field>
                <md-field :class="getValidationClass('rePassword')">
                  <label for="rePassword">비밀번호 확인</label>
                  <md-input
                    name="rePassword"
                    id="rePassword"
                    type="password"
                    v-model="form.rePassword"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">특수문자 포함 8자이상으로 설정해주세요.</span>
                  <span class="md-error" v-if="!$v.form.rePassword.required">The last name is required</span>
                  <span class="md-error" v-else-if="!$v.form.rePassword.minlength">8자이상으로 설정해주세요.</span>
                </md-field>
                <md-field :class="getValidationClass('name')">
                  <label for="hospitalName">병원 관리자 이름</label>
                  <md-input
                    name="name"
                    id="name"
                    v-model="form.name"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">병원 관리자 이름을 입력해주세요</span>
                  <span class="md-error" v-if="!$v.form.name.required">병원 관리자 이름을 입력해주세요</span>
                </md-field>
                <md-field :class="getValidationClass('hospital_name')">
                  <label for="hospitalName">병원명(영문)</label>
                  <md-input
                    name="hospital_name"
                    id="hospitalName"
                    v-model="form.hospital_name"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">병원명을 영문으로 입력해주세요</span>
                  <span class="md-error" v-if="!$v.form.hospital_name.required">병원명을 영문으로 입력해주세요</span>
                </md-field>
                <md-field :class="getValidationClass('hospital_address')">
                  <label for="address">주소(영문)</label>
                  <md-input
                    name="hospital_address"
                    id="address"
                    v-model="form.hospital_address"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">주소를 영문으로 입력해주세요</span>
                  <span class="md-error" v-if="!$v.form.hospital_address.required">The email is required</span>
                </md-field>
                <md-field :class="getValidationClass('email')">
                  <label for="phone">전화번호</label>
                  <md-input
                    name="hospital_phone"
                    id="phone"
                    v-model="form.hospital_phone"
                    :disabled="sending"
                  />
                  <span class="md-helper-text">전화번호를 입력해주세요</span>
                  <span class="md-error" v-if="!$v.form.hospital_phone.required">전화번호를 입력해주세요</span>
                </md-field>
              </div>
            </div>
            <div class="md-layout-item md-size-50 md-small-size-100">
              <div class="section">
                <p class="md-subheading">진료과목</p>
                <md-checkbox v-for="subject in subjects" :key="subject" v-model="form.subjects" :value="subject">{{subject}}</md-checkbox>
              </div>
              <div class="section">
                <p class="md-subheading">편의시설</p>
                <md-checkbox v-for="service in services" :key="service" v-model="form.services" :value="service">{{service}}</md-checkbox>
              </div>
            </div>
            <div class="md-layout-item md-size-100">
              <div class="section">
                <md-checkbox v-model="agreeAll">이용약관, 개인정보 수집 및 이용, 프로모션 안내 메일 수신(선택)에 모두 동의합니다.</md-checkbox><br>
                <md-checkbox v-model="agreeUse">아임닥터 이용약관 동의(필수)</md-checkbox>
                <md-field>
                  <md-textarea v-model="textarea" readonly />
                </md-field>
                <md-checkbox v-model="agreeUser">개인정보 수집 및 이용에 대한 안내(필수)</md-checkbox>
                <md-field>
                  <md-textarea v-model="textarea" readonly />
                </md-field>
                <md-checkbox v-model="agreeMailing">이벤트 등 프로모션 알림 메일 수신(선택)</md-checkbox>
              </div>
            </div>
          </md-card-content>
          <md-progress-bar md-mode="indeterminate" v-if="sending"/>
          <md-card-actions>
            <md-button type="submit" class="md-primary" :disabled="sending">동의하고 가입하기</md-button>
          </md-card-actions>
        </md-card>
        <md-snackbar :md-active.sync="userSaved">The user {{ lastUser }} was saved with success!</md-snackbar>
      </form>
    </div>
    <input id="pac-input" type="text" placeholder="Enter a location" @click="initMap">
    <div id="map"></div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
// import GoogleMapsLoader from 'google-maps'
import {
  required,
  email,
  minLength
} from 'vuelidate/lib/validators'

export default {
  name: 'SignUp',
  mixins: [validationMixin],
  data: () => ({
    form: {
      email: null,
      password: null,
      rePassword: null,
      name: null,
      hospital_name: null,
      hospital_phone: null,
      hospital_address: null,
      latitude: 3.3,
      longitude: 33,
      subjects: [],
      services: []
    },
    userSaved: false,
    sending: false,
    lastUser: null,
    subjects: ['내과', '이비인후과', '정형외과', '산부인과', '가정의학과',
      '비뇨기과', '한의과', '영상의학과', '외과', '치과', '안과', '피부과',
      '신경외과', '재활의학과'],
    services: ['주차가능', '휠체어 서비스', '24시간 닥터콜', '앰뷸런스 서비스',
      '통역서비스(영어)', '통역서비스(중국어)', '통역서비스(일본어)'],
    agreeAll: false,
    agreeUse: false,
    agreeUser: false,
    agreeMailing: false,
    textarea: ''
  }),
  validations: {
    form: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(8)
      },
      rePassword: {
        required,
        minLength: minLength(8)
      },
      name: {
        required
      },
      hospital_name: {
        required
      },
      hospital_phone: {
        required
      },
      hospital_address: {
        required
      }
    }
  },
  watch: {
    agreeAll: (val) => {
      console.log(this.agreeUse)
      this.agreeUse = val
      this.agreeUser = val
      this.agreeMailing = val
    }
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.form.firstName = null
      this.form.lastName = null
      this.form.age = null
      this.form.gender = null
      this.form.email = null
    },
    saveUser () {
      this.sending = true
      console.log(this.form)

      this.$http.put(`${this.$api}/auth/hospital`, this.form)
        .then((result) => {
          console.log(result)
          this.sending = false
          if (result.code === 200) this.$router.go('/hospital/home')
        })
    },
    validateUser () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.saveUser()
      }
    },
    initMap () {
    //   GoogleMapsLoader.load(function (google) {
    //     var map = new google.maps.Map(document.getElementById('map'), {
    //       center: {lat: -33.8688, lng: 151.2195},
    //       zoom: 13,
    //       mapTypeId: 'roadmap'
    //     })

    //     // Create the search box and link it to the UI element.
    //     var input = document.getElementById('pac-input')
    //     var searchBox = new google.maps.places.SearchBox(input)
    //     map.controls[google.maps.ControlPosition.TOP_LEFT].push(input)

    //     // Bias the SearchBox results towards current map's viewport.
    //     map.addListener('bounds_changed', function () {
    //       searchBox.setBounds(map.getBounds())
    //     })

    //     var markers = []
    //     // Listen for the event fired when the user selects a prediction and retrieve
    //     // more details for that place.
    //     searchBox.addListener('places_changed', function () {
    //       var places = searchBox.getPlaces()

    //       if (places.length === 0) {
    //         return
    //       }

    //       // Clear out the old markers.
    //       markers.forEach(function (marker) {
    //         marker.setMap(null)
    //       })
    //       markers = []

    //       // For each place, get the icon, name and location.
    //       var bounds = new google.maps.LatLngBounds()
    //       places.forEach(function (place) {
    //         if (!place.geometry) {
    //           return
    //         }
    //         var icon = {
    //           url: place.icon,
    //           size: new google.maps.Size(71, 71),
    //           origin: new google.maps.Point(0, 0),
    //           anchor: new google.maps.Point(17, 34),
    //           scaledSize: new google.maps.Size(25, 25)
    //         }

    //         // Create a marker for each place.
    //         markers.push(new google.maps.Marker({
    //           map: map,
    //           icon: icon,
    //           title: place.name,
    //           position: place.geometry.location
    //         }))

    //         if (place.geometry.viewport) {
    //         // Only geocodes have viewport.
    //           bounds.union(place.geometry.viewport)
    //         } else {
    //           bounds.extend(place.geometry.location)
    //         }
    //       })
    //       map.fitBounds(bounds)
    //    })
    //  })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.md-card{
  margin: 50px auto;
  .section{
    margin-bottom: 40px;
  }
}
</style>



