<template>
  <md-content>
    <md-tabs>
        <md-tab id="tab-hospital" md-label="병원">
          <form novalidate class="md-layout" @submit.prevent="validateUser">
            <md-card-content class="md-layout md-gutter md-alignment-top">
              <div class="md-layout-item md-medium-size-50 md-small-size-100">
                <div class="section">
                  <md-field :class="getValidationClass('email')">
                    <label for="email">병원명(영문)</label>
                    <md-input
                      name="email"
                      id="email"
                      v-model="form.email"
                      :disabled="sending"
                    />
                    <span class="md-helper-text">병원명을 영문으로 입력해주세요</span>
                    <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                    <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                  </md-field>
                  <md-field :class="getValidationClass('email')">
                    <label for="email">전화번호</label>
                    <md-input
                      name="email"
                      id="email"
                      v-model="form.email"
                      :disabled="sending"
                    />
                    <span class="md-helper-text">전화번호를 입력해주세요</span>
                    <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                    <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                  </md-field>
                  <md-field :class="getValidationClass('email')">
                    <label for="email">주소(영문)</label>
                    <md-input
                      name="email"
                      id="email"
                      v-model="form.email"
                      :disabled="sending"
                    />
                    <span class="md-helper-text">주소를 영문으로 입력해주세요</span>
                    <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                    <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                  </md-field>
                </div>
                <div class="section">
                  <p class="md-subheading">영업시간</p>
                  <div class="week-wrapper" v-for="week in weeks" :key="week">
                    <md-checkbox v-model="array2" :value="week">{{week}}</md-checkbox><br>
                    <select>
                      <option v-for="hour in hours" :key="hour" :value="hour">{{hour}}</option>
                    </select>
                    <select>
                      <option v-for="minute in minutes" :key="minute" :value="minute">{{minute}}</option>
                    </select>
                    ~
                    <select>
                      <option v-for="hour in hours" :key="hour" :value="hour">{{hour}}</option>
                    </select>
                    <select>
                      <option v-for="minute in minutes" :key="minute" :value="minute">{{minute}}</option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="md-layout-item md-medium-size-50 md-small-size-100">
                <div class="section">
                  <p class="md-subheading">진료과목</p>
                  <md-checkbox v-for="subject in subjects" :key="subject" v-model="array" :value="subject">{{subject}}</md-checkbox>
                </div>
                <div class="section">
                  <p class="md-subheading">편의시설</p>
                  <md-checkbox v-for="service in services" :key="service" v-model="array2" :value="service">{{service}}</md-checkbox>
                </div>
                <div class="section">
                  <p class="md-subheading">해시태그</p>
                  <p>본 병원에 해당되는 내용들을 해시태그 해주세요. 관련 정보 검색 시 노출할 수 있습니다.</p>
                  <md-chips v-model="hashtags"></md-chips>
                </div>
                <div class="section">
                  <p class="md-subheading">사진등록</p>
                  <p>(썸네일 이미지 사이즈는 최소 가로 317 세로 215 / png, jpg파일로 꼭 보내주세요.)</p>
                </div>
                <md-card-actions>
                  <md-button type="submit" class="md-primary" :disabled="sending">변경사항 저장</md-button>
                </md-card-actions>
              </div>
            </md-card-content>
            <md-progress-bar md-mode="indeterminate" v-if="sending"/>
          </form>
        </md-tab>
        <md-tab id="tab-doctor" md-label="의사">
            <form novalidate class="md-layout" @submit.prevent="validateUser">
            <md-card-content class="md-layout md-gutter md-alignment-top">
              <div class="md-layout-item md-medium-size-50 md-small-size-100">
                <div>
                  <input type="file">
                </div>
                <div class="section">
                  <md-field :class="getValidationClass('email')">
                    <label for="email">의사이름(한글)</label>
                    <md-input
                      name="email"
                      id="email"
                      v-model="form.email"
                      :disabled="sending"
                    />
                    <span class="md-helper-text">이름을 한글로 입력해주세요</span>
                    <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                    <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                  </md-field>
                  <md-field :class="getValidationClass('email')">
                    <label for="email">의사이름(영문)</label>
                    <md-input
                      name="email"
                      id="email"
                      v-model="form.email"
                      :disabled="sending"
                    />
                    <span class="md-helper-text">이름을 영문으로 입력해주세요</span>
                    <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
                    <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
                  </md-field>
                </div>
                <div class="section">
                  <p class="md-subheading">진료과목</p>
                  <md-checkbox v-for="subject in subjects" :key="subject" v-model="array" :value="subject">{{subject}}</md-checkbox>
                </div>
                <div class="section">
                  <p class="md-subheading">해시태그</p>
                  <p>관련 의사의 해시태그를 찾아서 입력해주세요.</p>
                  <md-chips v-model="hashtags"></md-chips>
                </div>
                <md-card-actions>
                  <md-button type="submit" class="md-primary" :disabled="sending">변경사항 저장</md-button>
                </md-card-actions>
              </div>
            </md-card-content>
            <md-progress-bar md-mode="indeterminate" v-if="sending"/>
          </form>
        </md-tab>
    </md-tabs>
  </md-content>
</template>

<script>
import { validationMixin } from 'vuelidate'
import {
  required,
  email,
  minLength,
  maxLength
} from 'vuelidate/lib/validators'

export default {
  name: 'HospitalEdit',
  mixins: [validationMixin],
  data: () => ({
    form: {
      firstName: null,
      lastName: null,
      gender: null,
      age: null,
      email: null
    },
    userSaved: false,
    sending: false,
    lastUser: null,
    subjects: ['내과', '이비인후과', '정형외과', '산부인과', '가정의학과',
      '비뇨기과', '한의과', '영상의학과', '외과', '치과', '안과', '피부과',
      '신경외과', '재활의학과'],
    services: ['주차가능', '휠체어 서비스', '24시간 닥터콜', '앰뷸런스 서비스',
      '통역서비스(영어)', '통역서비스(중국어)', '통역서비스(일본어)'],
    weeks: ['월요일', '화요일', '수요일', '목요일', '금요일', '토요일', '일요일', '점심시간'],
    hours: ['00시', '01시', '02시', '03시', '04시', '05시', '06시', '07시', '08시', '09시',
      '10시', '11시', '12시', '13시', '14시', '15시', '16시', '17시', '18시', '19시',
      '20시', '21시', '22시', '23시'
    ],
    minutes: ['00분', '10분', '20분', '30분', '40분', '50분'],
    array: [],
    array2: [],
    h: '',
    m: '',
    hashtags: []
  }),
  validations: {
    form: {
      firstName: {
        required,
        minLength: minLength(3)
      },
      lastName: {
        required,
        minLength: minLength(3)
      },
      age: {
        required,
        maxLength: maxLength(3)
      },
      gender: {
        required
      },
      email: {
        required,
        email
      }
    }
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.form.firstName = null
      this.form.lastName = null
      this.form.age = null
      this.form.gender = null
      this.form.email = null
    },
    saveUser () {
      this.sending = true
      // Instead of this timeout, here you can call your API
      window.setTimeout(() => {
        this.lastUser = `${this.form.firstName} ${this.form.lastName}`
        this.userSaved = true
        this.sending = false
        this.clearForm()
      }, 1500)
    },
    validateUser () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.saveUser()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.md-content{
  margin: 50px 30px;
  .section{
    margin-bottom: 40px;
  }
}
</style>



