<template>
  <div>
    <div class="first">
      <div class="left">
          <div class="left-nav">
            <img class="logo-img" src="" alt="로고이미지">
            <ul>
              <router-link to="/hospital">
              <li>가격</li>
              </router-link>
              <router-link to="/hospital/treatment">
              <li>회사소개</li>
              </router-link>
              <router-link to="/hospital/info">
              <li>문의</li>
              </router-link>
            </ul>
          </div>
          <div class="left-content">
            <div class="title">외국인 환자를<br>유치하고 싶으신가요?</div>
            <div class="content">Imdr. is created to bring a better
healthcare experience to foreigners  in Korea. With our services,
you won’t be facing your illness alone.
Connect with us today.
            </div>
            <p class="searchbar-title">Find Hospital</p>
            <md-field class="searchbar-wrapper">
              <md-input class="searchbar" v-model="email" />
            </md-field>
          </div>
      </div>
      <div class="right">
        <div class="right-nav">
          <ul>
            <router-link to="/sign_up">
            <li>회원가입</li>
            </router-link>
          </ul>
        </div>
        <form novalidate class="md-layout" @submit.prevent="validateUser">
          <md-card>
            <div class="title">로그인</div>
            <div class="sub-title">병원용 회원 가입하기</div>
            <md-field :class="getValidationClass('email')">
              <label for="email">이메일</label>
              <md-input
                v-model="form.email"
                autocomplete="email"
                id="email"
                type="email"
              />
              <span class="md-error" v-if="!$v.form.email.required">The email is required</span>
              <span class="md-error" v-else-if="!$v.form.email.email">Invalid email</span>
            </md-field>
            <md-field>
              <label>패스워드</label>
              <md-input
                name="password"
                id="password"
                type="password"
                v-model="form.password"
              />
              <span class="md-error" v-if="!$v.form.password.required">The last name is required</span>
              <span class="md-error" v-else-if="!$v.form.password.minlength">8자이상으로 설정해주세요.</span>
            </md-field>
            <div class="btn-wrapper">
              <div>
                <md-checkbox v-model="obj" :value="obj2">정보 기억하기</md-checkbox>
                <p class="forgot-password">비밀번호를 잊어버리셨나요?</p>
              </div>
              <md-button type="submit" class="btn-login md-raised" :md-ripple="false">로그인</md-button>
            </div>
            <md-button class="btn-google md-raised" :md-ripple="false">구글계정으로 가입하기</md-button>
          </md-card>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import {
  required,
  email,
  minLength
} from 'vuelidate/lib/validators'

export default {
  name: 'Landing',
  mixins: [validationMixin],
  data () {
    return {
      form: {
        email: null,
        password: null
      }
    }
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(8)
      }
    }
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    validateUser () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        this.logIn()
      }
    },
    logIn () {
      this.$store.dispatch('LOGIN', this.form)
        .then((result) => {
          if (result.status === 400) {
            alert(result.data.message)
          } else {
            this.$router.push('/hospital/home')
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.first{
    display: flex;
    height: 2067px;
    width: 100%;
    .left{
        width: 60%;
        min-width: 450px;
        margin-left:50px;
        .left-nav{
            .logo-img{
                width: 94px;
                height: 46px;
                margin-right: 120px;
            }
            ul{
                display: inline-block;
                padding-left: 0;
                li{
                    list-style: none;
                    display: inline-block;
                    font-size: 20px;
                    line-height: 4.5;
                    color: #272727;
                }
                a+a{
                    margin-left: 98px;
                }
            }
        }
        .left-content{
            width: 910px;
            margin-right: 40px;
            margin-left: auto;
             .title{
                font-family: NotoSansCJKkr;
                font-size: 70px;
                font-weight: 500;
                line-height: 1.14;
                letter-spacing: -1.8px;
                color: #1c858e;
                margin-bottom: 20px;
                margin-top: 334px;
            }
            .content{
                font-family: Helvetica Neue;
                font-size: 18px;
                line-height: 1.44;
                text-align: left;
                color: #000000;
                white-space: pre;
                margin-bottom: 61px;
            }
            .searchbar-title{
                font-size: 20px;
                color: #1c858e;
                margin-bottom: 14px;
            }
            .searchbar-wrapper{
                width: 910px;
                height: 62.4px;
                border-radius: 7px;
                background-color: #1c858e;
                padding: 5px;
                .searchbar{
                    max-width: 786.5px;
                    height: 52.6px;
                    border-radius: 9px;
                    background-color: #ffffff;
                }
            }
        }
    }
    .right{
        background-color: #526d91;
        width: 40%;
        min-width: 650px;

        .right-nav{
            li{
                list-style: none;
                font-size: 20px;
                line-height: 4.5;
                letter-spacing: -0.5px;
                color: #ffffff;
            }
        }
        .md-card{
            width: 576px;
            height: 541px;
            border-radius: 9px;
            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
            background-color: #ffffff;
            padding: 34px 51px;
            margin-top: 297px;
            margin-left: 41px;

            .title{
                font-size: 35px;
                line-height: 0.57;
                color: #000000;
                margin-bottom: 3px;
            }
            .sub-title{
                font-size: 18px;
                line-height: 1.5;
                color: #1c858e;
                margin-bottom: 52px;
            }
            .btn-wrapper{
                display: flex;
                width: 100%;

                .forgot-password{
                    font-size: 18px;
                    line-height: 1.5;
                    color: #1c858e;
                    text-decoration: underline;
                    cursor: pointer;
                }
                .btn-login{
                    float: right;
                    width: 125px;
                    height: 55px;
                    font-size: 20px;
                    margin-left: auto;
                }
            }

            .btn-google{
                width: 100%;
                height: 57px;
                font-size: 20px;
                margin-top: 33px;
            }
        }
    }
}
</style>



