import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const resourceHost = 'https://ec2-13-209-66-34.ap-northeast-2.compute.amazonaws.com:5000'

export default new Vuex.Store({
  state: {
    // token: null
    token: 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzUxMiJ9.eyJpZCI6bnVsbCwiZW1haWwiOiJ6aW80MjcyMUBuYXZlci5jb20iLCJwYXNzd29yZCI6ImRmNTFhNWYyZWE5NTk5NTU5MzgxNmFlOTg2YzMzMjc4In0.mD0D8qoU1cDV6x9CUZxrXMWF4Fv0uLmWJZOZXdaV1vGfL2JM7vz7Q1UGdS3Ixqh-CDVrLNXOZHaFBJBK6Pm8Uw'
  },
  getters: {

  },
  mutations: {
    LOGIN (state, result) {
      let token = result.data.data.user.token
      state.token = token
    },
    LOGOUT (state) {
      state.token = null
    }
  },
  actions: {
    LOGIN ({commit}, {email, password}) {
      let form = new FormData()
      form.append('email', email)
      form.append('password', password)

      return axios.post(`${resourceHost}/auth/hospital`, form)
        .then((result) => {
          commit('LOGIN', result)
          return true
        })
        .catch(error => error.response)
    },
    LOGOUT ({commit}) {
      commit('LOGOUT')
    }
  }
})



