<template>
    <md-content>
        <md-tabs :md-dynamic-height="true">
            <md-tab id="tab-hospital" md-label="일정">
              <button @click="change">d</button>
              <FullCalendar
                ref="fullCalendar"
                timeZone= 'UTC'
                defaultView="timeGridWeek"
                :locale= "locale"
                :resources="resources"
                :plugins="calendarPlugins"
                :events="events"
                @eventClick="handleDateClick"
              />
            </md-tab>

            <md-tab id="tab-doctor" md-label="진료내역">
              <md-table v-model="people" md-card @md-selected="onSelect">
                <md-table-toolbar>
                  <h1 class="md-title">Selection Colors</h1>
                </md-table-toolbar>

                <md-table-row slot="md-table-row" slot-scope="{ item }" :class="getClass(item)" md-selectable="single">
                  <md-table-cell md-label="번호" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
                  <md-table-cell md-label="상태" md-sort-by="name">{{ item.name }}</md-table-cell>
                  <md-table-cell md-label="이름" md-sort-by="email">{{ item.email }}</md-table-cell>
                  <md-table-cell md-label="전화번호" md-sort-by="gender">{{ item.gender }}</md-table-cell>
                  <md-table-cell md-label="날짜" md-sort-by="title">{{ item.title }}</md-table-cell>
                </md-table-row>
              </md-table>
            </md-tab>
        </md-tabs>
    </md-content>
</template>
<script>
import FullCalendar from '@fullcalendar/vue'
import resourceTimeGridPlugin from '@fullcalendar/resource-timegrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import koLocale from '@fullcalendar/core/locales/ko'
let calendarApi
export default {
  name: 'HospitalTreatment',
  components: {
    FullCalendar
  },
  created () {
    this.$http.get(`${this.$api}/diagnosis`, {
      headers: {
        'X-Http-Token': this.$store.state.token
      },
      params: {
        month: '5'
      }
    })
      .then((result) => {
        console.log(result)
      })
  },
  mounted () {
    calendarApi = this.$refs.fullCalendar.getApi()
    calendarApi.render()
  },
  data () {
    return {
      selected: {},
      people: [
        {
          id: 1,
          name: 'Shawna Dubbin',
          email: 'sdubbin0@geocities.com',
          gender: 'Male',
          title: 'Assistant Media Planner'
        },
        {
          id: 2,
          name: 'Odette Demageard',
          email: 'odemageard1@spotify.com',
          gender: 'Female',
          title: 'Account Coordinator'
        },
        {
          id: 3,
          name: 'Lonnie Izkovitz',
          email: 'lizkovitz3@youtu.be',
          gender: 'Female',
          title: 'Operator'
        },
        {
          id: 4,
          name: 'Thatcher Stave',
          email: 'tstave4@reference.com',
          gender: 'Male',
          title: 'Software Test Engineer III'
        },
        {
          id: 5,
          name: 'Clarinda Marieton',
          email: 'cmarietonh@theatlantic.com',
          gender: 'Female',
          title: 'Paralegal'
        }
      ],
      events: [
        {
          resourceId: 'reservation',
          title: '예약진행중',
          color: '#ffc567',
          start: '2019-05-19T09:00:00',
          end: '2019-05-19T09:30:00'
        },
        {
          resourceId: 'reception',
          title: '예약진행중',
          color: '#359a9c',
          start: '2019-05-19T13:30:00',
          end: '2019-05-19T14:00:00'
        },
        {
          resourceId: 'diagnosis_complete',
          title: '예약진행중',
          color: '#526d91',
          start: '2019-05-19T14:00:00',
          end: '2019-05-19T14:30:00'
        }
      ],
      calendarPlugins: [ resourceTimeGridPlugin, timeGridPlugin ],
      locale: koLocale,
      resources: [
        { id: 'reservation', title: '예약진행중' },
        { id: 'reception', title: '예약완료' },
        { id: 'diagnosis_complete', title: '진료완료' }
      ]
    }
  },
  methods: {
    getClass: ({ id }) => ({
      'md-primary': id === 2,
      'md-accent': id === 3
    }),
    onSelect (item) {
      this.selected = item
    },
    change () {
      calendarApi.changeView('resourceTimeGridDay')
    },
    handleDateClick (arg) {
      console.log(arg)
      alert('대충 팝업이 올라온다는 내용')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.md-content{
  margin: 50px 30px;
  .md-tab{
    min-height: 700px;
  }
}
</style>



