<template>
  <div>
        <div class="first">
            <div class="left">
                <md-card>
                    <div class="title">로그인</div>
                    <div class="sub-title">아임닥터 어드민 페이지</div>
                    <md-field>
                        <label>이메일</label>
                        <md-input v-model="email" type="email"></md-input>
                    </md-field>
                    <md-field>
                        <label>패스워드</label>
                        <md-input v-model="password" type="password"></md-input>
                    </md-field>
                    <div class="btn-wrapper">
                        <div>
        `                   <md-checkbox v-model="obj" :value="obj2">정보 기억하기</md-checkbox>
                        </div>
                        <md-button class="btn-login md-raised" :md-ripple="false">로그인</md-button>
                    </div>
                </md-card>
            </div>
            <div class="right">
            </div>
        </div>
  </div>
</template>

<script>
export default {
  name: 'Landing',
  data () {
    return {
      msg: 'Welcome to Your Vue.js sdfsd'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.first{
    display: flex;
    width: 100%;
    .left{
        width: 60%;
        min-width: 950px;
        margin-left:50px;

        .md-card{
            width: 576px;
            height: 541px;
            border-radius: 9px;
            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
            background-color: #ffffff;
            padding: 54px 51px;
            margin-top: 297px;
            margin-left: 41px;

            .title{
                font-size: 35px;
                line-height: 0.57;
                color: #000000;
                margin-bottom: 3px;
            }
            .sub-title{
                font-size: 18px;
                line-height: 1.5;
                color: #1c858e;
                margin-bottom: 52px;
            }
            .btn-wrapper{
                display: flex;
                width: 100%;

                .forgot-password{
                    font-size: 18px;
                    line-height: 1.5;
                    color: #1c858e;
                    text-decoration: underline;
                    cursor: pointer;
                }
                .btn-login{
                    float: right;
                    width: 125px;
                    height: 55px;
                    font-size: 20px;
                    margin-left: auto;
                }
            }

            .btn-google{
                width: 100%;
                height: 57px;
                font-size: 20px;
                margin-top: 33px;
            }
        }
    }
    .right{
        width: 40%;
        min-width: 650px;
    }
}
</style>



