<template>
    <div>
        <div class="sidebar">
            <img src="" alt="imdr" class="logo-img" />
            <div class="hospital-name">
                Admin<span>></span><br>
            </div>
            <ul class="side-menu">
                <router-link to="/admin/service_list">
                  <li>서비스 진행내역<div v-show="links[0]===nowPage" class="selected" /></li>
                </router-link>
                <router-link to="/admin/user_list">
                  <li>유저<div v-show="links[1]===nowPage" class="selected" /></li>
                </router-link>
                <router-link to="/admin/hospital_list">
                  <li>병원<div v-show="links[1]===nowPage" class="selected" /></li>
                </router-link>
                <router-link to="/admin/setting">
                  <li>어드민관리<div v-show="links[1]===nowPage" class="selected" /></li>
                </router-link>
            </ul>
            <router-link to="/foo" class="sign-out">Sign out</router-link>
        </div>
        <div class="body">
            <div class="nav">
            </div>
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {
  name: 'HospitalBasic',
  data () {
    return {
      nowPage: '',
      links: ['HospitalHome', 'HospitalTreatment', 'Hospital', 'HospitalInfo']
    }
  },
  mounted: function () {
  },
  watch: {
    $route (to, from) {
      this.nowPage = to.name
    }
  },
  computed: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.sidebar{
    width: 288px;
    height: 100vh;
    background-color: #359a9c;
    position: fixed;
    left: 0;
    padding-top: 48px;
    padding-left: 54px;
    padding-right: 31px;

    .logo-img{
        width: 88px;
        height: 44px;
        margin-bottom: 31px;
    }
    .hospital-name{
        width: 100%;
        color: #ffc567;
        line-height: 1.5;
        font-size: 20px;
        font-family: SDGothicNeoa-dRg;
        padding-bottom: 31px;
        border-bottom: 1px solid #ffc567;
        margin-bottom: 40px;

        span{
            float: right;
        }
    }
    .side-menu{
        padding-left: 0;

        li{
            height: 53px;
            display: flex;
            align-items: center;
            cursor: pointer;
            font-size: 16px;
            line-height: 5.63;
            color: #ffffff;
            position: relative;
            padding-left: 30px;

            .selected{
                background-color: rgba(0, 0, 0, 0.1);
                width: 288px;
                height: 53px;
                position: absolute;
                left: -54px;
            }
        }
    }
    .sign-out{
        line-height: 4.5;
        color: #ffffff;
        position: fixed;
        bottom: 30px;
    }
}
.body{
    width: 100vw;
    min-height: 100vh;
    padding-bottom: 50px;
    padding-left: 288px;
    background-color: #a8b8cc;
    .nav{
        width: 100%;
        height: 70px;
        box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.04);
        background-color: #ffffff;
    }
    .md-content{
        background: #ffffff;
    }
}
</style>



