import Vue from 'vue'
import Router from 'vue-router'
import VueMaterial from 'vue-material'
import SuiVue from 'semantic-ui-vue'
import axios from 'axios'

import Landing from '@/pages/Landing'
import SignUp from '@/pages/SignUp'

import HospitalBasic from '@/components/HospitalBasic'
import HospitalHome from '@/pages/hospital/Home'
import HospitalTreatment from '@/pages/hospital/Treatment'
import HospitalInfo from '@/pages/hospital/Info'
import HospitalEdit from '@/pages/hospital/Edit'

import AdminLogIn from '@/pages/admin/LogIn'
import AdminBasic from '@/components/AdminBasic'
import AdminServiceList from '@/pages/admin/ServiceList'
import AdminServiceDetail from '@/pages/admin/ServiceDetail'
import AdminUserList from '@/pages/admin/UserList'
import AdminUserDetail from '@/pages/admin/UserDetail'
import AdminHospitalList from '@/pages/admin/HospitalList'
import AdminHospitalDetail from '@/pages/admin/HospitalDetail'
import AdminSetting from '@/pages/admin/Setting'

import 'semantic-ui-css/semantic.min.css'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import '@fullcalendar/core/main.css'
import '@fullcalendar/timegrid/main.css'

Vue.use(VueMaterial)
Vue.use(Router)
Vue.use(SuiVue)

Vue.prototype.$http = axios
Vue.prototype.$api = 'http://ec2-13-209-66-34.ap-northeast-2.compute.amazonaws.com:5000'

// const requireAuth = () => (from, to, next) => {
//   const isAuthenticated = false
//   if (isAuthenticated) return next()
//   next('/login?returnPath=me')
// }

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing
    },
    {
      path: '/sign_up',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/admin_login',
      name: 'AdminLogIn',
      component: AdminLogIn
    },
    {
      path: '/hospital',
      name: 'HospitalBasic',
      redirect: { name: 'HospitalHome' },
      component: HospitalBasic,
      //   beforeEnter: requireAuth(),
      children: [
        {
          path: 'home',
          name: 'HospitalHome',
          component: HospitalHome
        },
        {
          path: 'treatment',
          name: 'HospitalTreatment',
          component: HospitalTreatment
        },
        {
          path: 'info',
          name: 'HospitalInfo',
          component: HospitalInfo
        },
        {
          path: 'edit',
          name: 'HospitalEdit',
          component: HospitalEdit
        }
      ]
    },
    {
      path: '/admin',
      name: 'AdminBasic',
      redirect: { name: 'AdminServiceList' },
      component: AdminBasic,
      children: [
        {
          path: 'service_list',
          name: 'AdminServiceList',
          component: AdminServiceList
        },
        {
          path: 'service_detail',
          name: 'AdminServiceDetail',
          component: AdminServiceDetail
        },
        {
          path: 'user_list',
          name: 'AdminUserList',
          component: AdminUserList
        },
        {
          path: 'user_detail',
          name: 'AdminUserDetail',
          component: AdminUserDetail
        },
        {
          path: 'hospital_list',
          name: 'AdminHospitalList',
          component: AdminHospitalList
        },
        {
          path: 'hospital_detail',
          name: 'AdminHospitalDetail',
          component: AdminHospitalDetail
        },
        {
          path: 'setting',
          name: 'AdminSetting',
          component: AdminSetting
        }
      ]
    }
  ]
})



