                </md-card-header>
                <md-card-actions md-alignment="left">
                  <md-button>열람하기</md-button>
                  <md-button>다운로드</md-button>
                </md-card-actions>
              </md-card>
            </div>
            <div class="md-layout-item md-medium-size-33 md-small-size-33 md-xsmall-size-100">
              <md-card>
