          <md-table v-model="people" md-card @md-selected="onSelect">
            <md-table-row slot="md-table-row" slot-scope="{ item }" :class="getClass(item)" md-selectable="single">
              <md-table-cell md-label="번호" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
              <md-table-cell md-label="시작일자" md-sort-by="name">{{ item.name }}</md-table-cell>
              <md-table-cell md-label="진료병원" md-sort-by="email">{{ item.email }}</md-table-cell>
              <md-table-cell md-label="상태" md-sort-by="gender">{{ item.gender }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-content>
      </div>
    </div>
