</template>
<script>
import HospitalBasic from '@/components/HospitalBasic'
export default {
  name: 'Landing',
  components: {
    HospitalBasic
  },
  data () {
    return {
      selected: {},
      people: [
        {
          id: 1,
          name: 'Shawna Dubbin',
          email: 'sdubbin0@geocities.com',
          gender: 'Male',
          title: 'Assistant Media Planner'
