          id: 5,
          name: 'Clarinda Marieton',
          email: 'cmarietonh@theatlantic.com',
          gender: 'Female',
          title: 'Paralegal'
        }
      ]
    }
  },
  methods: {
    getClass: ({ id }) => ({
      'md-primary': id === 2,
      'md-accent': id === 3
    }),
    onSelect (item) {
      this.$router.push('service_detail')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.wrapper{
  padding: 30px;
  max-width: 1500px;
  margin: 0 auto;
  .main-content{
      padding: 20px 30px;
      margin-bottom: 20px;
  }
  .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .image-card{
    margin: 20px auto;
    height: 200px;
    width: 200px;
  }
}
</style>



