  created () {
    this.$http.get(`${this.$api}/hospital_info`, {
      headers: {
        'X-Http-Token': this.$store.state.token
      }
    })
      .then((result) => {
        console.log(result)
        this.hospitalData = result.data.data.hospital
      })
  },
  methods: {
    getClass: ({ id }) => ({
      'md-primary': id === 2,
      'md-accent': id === 3
    }),
    onSelect (item) {
      this.selected = item
    }
  }
}
