<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.card-wrapper{
  margin: 50px 0;
  padding: 0 30px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  .md-card{
    width: 30%;
