<template>
    <md-content>
        <md-table v-model="people" md-card @md-selected="onSelect">
          <md-table-toolbar>
            <h1 class="md-title">유저목록</h1>
          </md-table-toolbar>
          <md-table-row slot="md-table-row" slot-scope="{ item }" :class="getClass(item)" md-selectable="single">
            <md-table-cell md-label="번호" md-sort-by="id" md-numeric>{{ item.id }}</md-table-cell>
            <md-table-cell md-label="이름" md-sort-by="name">{{ item.name }}</md-table-cell>
            <md-table-cell md-label="국적" md-sort-by="email">{{ item.email }}</md-table-cell>
            <md-table-cell md-label="이메일" md-sort-by="gender">{{ item.gender }}</md-table-cell>
            <md-table-cell md-label="전화번호" md-sort-by="title">{{ item.title }}</md-table-cell>
            <md-table-cell md-label="인증여부" md-sort-by="title">{{ item.title }}</md-table-cell>
          </md-table-row>
        </md-table>
    </md-content>
</template>
<script>
export default {
  name: 'AdminUserList',
  components: {
  },
  data () {
    return {
      selected: {},
      people: [
        {
          id: 1,
          name: 'Shawna Dubbin',
          email: 'sdubbin0@geocities.com',
          gender: 'Male',
          title: 'Assistant Media Planner'
        },
        {
          id: 2,
          name: 'Odette Demageard',
          email: 'odemageard1@spotify.com',
          gender: 'Female',
          title: 'Account Coordinator'
        },
        {
          id: 3,
          name: 'Lonnie Izkovitz',
          email: 'lizkovitz3@youtu.be',
          gender: 'Female',
          title: 'Operator'
        },
        {
          id: 4,
          name: 'Thatcher Stave',
          email: 'tstave4@reference.com',
          gender: 'Male',
          title: 'Software Test Engineer III'
        },
        {
          id: 5,
          name: 'Clarinda Marieton',
          email: 'cmarietonh@theatlantic.com',
          gender: 'Female',
          title: 'Paralegal'
        }
      ]
    }
  },
  methods: {
    getClass: ({ id }) => ({
      'md-primary': id === 2,
      'md-accent': id === 3
    }),
    onSelect (item) {
      this.$router.push('user_detail')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.md-content{
    margin: 50px 30px;
    .md-tab{
      min-height: 700px;
    }
}
</style>



